export default {
	data() {
		return {
			showModal: false
		};
	},
	props: {
		confirmText: {
			type: String,
			default: '确定'
		},
		// 取消按钮文字
		cancelText: {
			type: String,
			default: '取消'
		},
		// 是否显示确定按钮
		confirmButton: {
			type: Boolean,
			default: true
		},
		// 是否显示取消按钮
		cancelButton: {
			type: Boolean,
			default: true
		},
	},
	methods: {
		close() {
			this.showModal = false;
			this.$emit('hide');
		},
		confirm() {
			this.showModal = false;
			this.$emit('confirm');
		},
		show() {
			this.showModal = true;
		}
	}

}
